.button-Type{
	width: 20%;
	border-radius: 4px;
  background-color: #353535;
  border: none;
  color: #FFFFFF;
  text-align: center;
  font-size: 28px;
  padding: 20px;
  transition: all 0.5s;
  cursor: pointer;
  margin: 5px;
}


.button-Type span {
	font-size: 2rem;
    font-weight: 300;
    line-height: 1.2;
	cursor: pointer;
	display: inline-block;
	position: relative;
	transition: 0.5s;
  }
  
  .button-Type span:after {
	content: '\00bb';
	position: absolute;
	opacity: 0;
	top: 0;
	right: -20px;
	transition: 0.5s;
  }
  
  .button-Type:hover span {
	padding-right: 25px;
  }
  
  .button-Type:hover span:after {
	opacity: 1;
	right: 0;
  }








